"use client";

const products = [
  { id: 1, name: "iPhone 14", price: 150000, image: "/p1.png" },
  { id: 2, name: "Dell Laptop", price: 180000, image: "/p2.png" },
  { id: 3, name: "Headphones", price: 12000, image: "/p3.png" },
  { id: 4, name: "Smart Watch", price: 30000, image: "/p4.png" },
];

export default function FeaturedProducts() {
  return (
    <section className="mt-10">
      <h2 className="text-2xl font-semibold mb-4">Featured Products</h2>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
        {products.map(product => (
          <div
            key={product.id}
            className="border rounded-2xl p-4 shadow-sm hover:shadow-md"
          >
            <img
              src={product.image}
              alt={product.name}
              className="w-full h-40 object-contain"
            />

            <h3 className="mt-3 font-semibold">{product.name}</h3>

            <p className="text-gray-700">Rs. {product.price}</p>

            <button className="mt-3 w-full bg-black text-white py-2 rounded-xl">
              Add to Cart
            </button>
          </div>
        ))}
      </div>
    </section>
  );
}
