"use client";

import { ShoppingBag, ArrowRight, Sparkles } from "lucide-react";

export default function Hero() {
  return (
    <section className="relative w-full overflow-hidden rounded-[2.5rem] bg-gradient-to-br from-white to-gray-100 p-8 md:p-16 shadow-sm border border-gray-200/50">
      {/* Decorative Background Element */}
      <div className="absolute -top-24 -right-24 h-96 w-96 rounded-full bg-indigo-500/5 blur-[100px]" />
      
      <div className="relative z-10 flex flex-col md:flex-row items-center justify-between gap-12">
        <div className="max-w-2xl space-y-8 text-center md:text-left">
          
          {/* Badge */}
          <div className="inline-flex items-center gap-2 rounded-full bg-white px-4 py-1.5 text-sm font-medium shadow-sm border border-gray-100">
            <Sparkles className="h-4 w-4 text-indigo-600" />
            <span className="text-gray-600">New season arrivals are here</span>
          </div>

          <h1 className="text-5xl md:text-7xl font-extrabold tracking-tight text-slate-900 leading-[1.1]">
            Shop the <span className="text-transparent bg-clip-text bg-gradient-to-r from-indigo-600 to-violet-600">Future</span> of Retail.
          </h1>

          <p className="text-lg md:text-xl text-gray-500 leading-relaxed max-w-lg">
            Experience curated collections featuring the latest in electronics, fashion, and home tech—optimized for your lifestyle.
          </p>

          <div className="flex flex-col sm:flex-row items-center gap-4">
            <button className="group flex items-center gap-2 bg-black text-white px-8 py-4 rounded-2xl font-bold transition-all hover:bg-gray-800 hover:shadow-xl active:scale-95">
              <span>Start Shopping</span>
              <ArrowRight className="h-5 w-5 transition-transform group-hover:translate-x-1" />
            </button>
            
            <button className="flex items-center gap-2 px-8 py-4 rounded-2xl font-bold text-gray-900 hover:bg-gray-50 transition-colors">
              <ShoppingBag className="h-5 w-5" />
              View Deals
            </button>
          </div>

          {/* Social Proof / Stats */}
          <div className="pt-4 flex items-center justify-center md:justify-start gap-8 border-t border-gray-200/60">
            <div>
              <p className="text-2xl font-bold text-gray-900">12k+</p>
              <p className="text-xs uppercase tracking-widest text-gray-400 font-semibold">Products</p>
            </div>
            <div className="h-8 w-px bg-gray-200" />
            <div>
              <p className="text-2xl font-bold text-gray-900">4.9/5</p>
              <p className="text-xs uppercase tracking-widest text-gray-400 font-semibold">Rating</p>
            </div>
          </div>
        </div>

        {/* Enhanced Image Container */}
        <div className="relative group">
          <div className="absolute -inset-4 rounded-[3rem] bg-gradient-to-tr from-indigo-500/10 to-purple-500/10 opacity-0 group-hover:opacity-100 transition-opacity duration-500 blur-2xl" />
          <div className="relative overflow-hidden rounded-[2rem] bg-gray-200 shadow-2xl transition-transform duration-500 group-hover:scale-[1.02]">
             {/* Replace with your actual image path */}
            <img
              src="/hero.png" 
              className="w-full max-w-[450px] object-cover h-[500px]"
              alt="Premium shopping experience"
            />
          </div>
        </div>
      </div>
    </section>
  );
}
