"use client";

const categories = [
  { id: 1, name: "Electronics" },
  { id: 2, name: "Fashion" },
  { id: 3, name: "Home & Kitchen" },
  { id: 4, name: "Sports" },
  { id: 5, name: "Beauty" },
];

export default function CategoryList() {
  return (
    <section className="mt-10">
      <h2 className="text-2xl font-semibold mb-4">Shop by Category</h2>

      <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
        {categories.map(cat => (
          <div
            key={cat.id}
            className="p-6 border rounded-xl shadow-sm hover:shadow-md cursor-pointer text-center"
          >
            {cat.name}
          </div>
        ))}
      </div>
    </section>
  );
}
